@import url('https://fonts.googleapis.com/css2?family=Urbanist:wght@200;300;400;600;700&display=swap');

.xy {
    display: none;
}

.scontainer {
    display: flex;
    justify-content: center;
    overflow-x: hidden;
    height: 100vh;
    font-family: 'Urbanist', sans-serif;
}

.container {
    width: 100%;
    height: 100%;
    max-width: 600px;
    max-height: 600px;
    display: flex;
    transform-style: preserve-3d;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.cards {
    position: relative;
    height: 330px;
    width: 430px;
    perspective: 1000px;
    transform-style: preserve-3d;

    @media (max-width: 600px) {
        height: 200px; 
        width: 70%; 
    }
}

.cards label {
    position: absolute;
    width: 430px;
    left: 0;
    right: 0;
    margin: auto;
    cursor: pointer;
    transition: transform 0.55s ease;

    @media (max-width: 600px) {
        width: 100%; 
    }
}

.cards .card {
    position: relative;
    height: 100%;
    background-color: #323444;
    border-radius: 10px;
}

.card .image {
    display: flex;
    justify-content: space-between;
}

.card .image img {
    border-radius: 5px;
    box-shadow: 15px 15px 40px rgba(0, 0, 0, 50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card .image .dots {
    text-align: center;
}

.dots div {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-bottom: 10px;
}

.dots div:nth-child(1) {
    background-color: var(--current-color1);
}

.dots div:nth-child(2) {
    background-color: #5B85F9;
}

.dots div:nth-child(3) {
    background-color: #000;
}

.infos .name {
    font-size: 30px;
    color: var(--current-color1);
    letter-spacing: 8px;
    margin-bottom: 20px;
    padding-left: 25%;
    transition: all .6s ease;

    @media (max-width: 600px) {
        font-size: 20px; 
        padding-left: 0; 
        text-align: center; 
    }
}

.infos .lorem {
    font-size: 15.5px;
    color: #ECEAED;
    letter-spacing: 3px;

    @media (max-width: 600px) {
        font-size: 12px; 
    }
}

.card .btn-contact {
    width: 100%;
    height: 60px;
    margin: 35px auto;
    border-radius: 5px;
    background-color: var(--current-color1);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 25px;
    letter-spacing: 10px;
    font-weight: 700;
    transition: all .7s ease;

    @media (max-width: 600px) {
        font-size: 20px;
        height: 40px; 
    }
}

